body {
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
}

.app {
  background-color: #ccc;
  padding: 50px;
  margin: 0 auto;
  height: 100vh;
  max-width: 1200px;
}

.wordbox {
  width: 80%;
  height: 80px;
  padding: 5px;
  overflow: hidden;
  font-size: 30px;
  margin: 0 auto;
  background-color: #fff;
}

.info {
  margin: 10px auto;

}

.time-remain-wrapper {
  text-align: left;
  display: inline-block;
  width: 50%;
}
.wpm-wrapper {
  text-align: right;
  display: inline-block;
  width: 50%;
}
.time-remain {
  color: red;
}

.wpm {
  color: green;
}

.input-wrapper{
  margin-top: 40px;
  text-align: center;
}
input {
  position: relative;
  width: 30%;
  height: 45px;
  padding: 5px;
  border-radius: 10px;
  font-size: 30px;
  &:focus {
    border: 2px green solid;
  }
}

button{
  position: absolute;
  width: 5%;
  height: 45px;
  padding: 5px;
  border-radius: 10px;
  font-size: 20px;
}